<template>
  <div class="alert">
    <div
      class="alert-main"
      v-for="item in notices"
      :key="item.name"
    >
      <div class="alert-content">
        <div class="conten">
          <i class="alert-icon" :class="'alert-icon-' + item.type"></i>
          <span class="alert-text">{{ item.content }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
let seed = 0;

function getUuid() {
  return "alert_" + seed++;
}

export default {
  name: "Alert",
  data() {
    return {
      notices: []
    };
  },
  methods: {
    add(notice) {
      const name = getUuid();

      let _notice = Object.assign(
        {
          name
        },
        notice
      );

      this.notices.push(_notice);

      // 定时移除，单位：秒
      const duration = notice.duration;
      setTimeout(() => {
        this.remove(name);
      }, duration * 1000);
    },
    remove(name) {
      let index = this.notices.findIndex(item => item.name === name);
      this.notices.splice(index, 1);
    }
  }
};
</script>
